import React, {Component} from 'react';

class About extends Component{
    render(){
        return(
        <div>
            <h1 style={{fontFamily:"Lobster"}}> About Me</h1>
            <p style={{fontSize:"30px" , fontFamily:"Dancing Script"}}> Aspirant of a career which can enhance my skills and fulfil my curiosity. </p>
            </div>
        );
    }
}

export default About;



